.body{
    padding: 30px;
    flex: 0.8;
    height: 100vh;
    width: 100%;
    color: white;
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
    overflow-y: overlay;
}

.body::-webkit-scrollbar{
    display: none;
}

.body__info{
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__infoText{
    flex: 1;
    margin-left: 16px;
}

.body__infoText > img{
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__icons{
    display: flex;
    align-items: center;
}

.body__icons > .MuiSvgIcon-root{
    margin-right: 20px;
}

.body__shuffle{
    font-size: 60px !important;
    margin: 20px 20px 20px 50px;
}

.body__icon--img{
    margin-right: 20px;
}

.body__songs{
    z-index: 1;
    margin: 20px -30px;
    display: flex;
    align-items: center;
}

.body__shuffle:hover{
    transition: 100ms transform ease-in;
    transform: scale(1.08);
}

.body__background {
    position: absolute;
    top: 13vh;
    right: 0;
    opacity: 0.4;
    height: 80%;
    width: 80%;
    background-color: black;
  }

.body__infoText > h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.body__infoText > p{
    font-size: 14px;
}
